#logo {
  height: 300px;
  width: 40%;
  background: url("logo_UI.7f49b92b.svg") center / contain no-repeat;
  margin-top: 20px;
  margin-bottom: 20px;
}

#splash_screen p {
  font-weight: 500;
}

#enemy, #character {
  width: 50%;
}

#enemy_col {
  width: 100px;
  height: 100px;
  background-color: #95ad91;
  margin: 15% auto auto;
}

#info {
  margin-top: 70px;
  margin-bottom: 40px;
  font-style: italic;
}

.active {
  visibility: visible;
}

.inactive {
  visibility: hidden;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

#battle {
  height: min-content;
  width: 45%;
  z-index: 2;
  color: #c1c3c5;
  background-color: #44464a;
  flex-direction: row;
  gap: 20px;
  margin: auto;
  padding: 30px 40px;
  display: flex;
  position: relative;
  top: 20%;
}

#hp_text, #enemy_hp_text {
  text-align: right;
  margin-top: 10px;
  margin-bottom: 20px;
  font-size: 1em;
}

h1 {
  font-size: 2em_;
  padding: 15px;
}

#hp_bar, #enemy_hp_bar {
  background-color: #c1c3c5;
  padding: 8px;
  display: flex;
}

#hp_green, #enemy_hp_green {
  width: 100%;
}

progress {
  -webkit-appearance: none;
}

::-webkit-progress-bar {
  background-color: #c1c3c5;
}

::-webkit-progress-value {
  background-color: #95ad91;
}

#descriptor_en {
  margin: auto;
  font-style: italic;
  position: absolute;
  bottom: 50px;
}

#descriptor_ch {
  margin: auto;
  font-style: italic;
  position: absolute;
  bottom: 80px;
}

.action {
  cursor: pointer;
  background-color: #65686c;
  flex-direction: row;
  align-items: center;
  gap: 30px;
  margin: 5px;
  padding: 8px 35px 8px 15px;
  font-size: 1.5em;
  display: flex;
}

.circle_bg {
  width: 45px;
  height: 45px;
  background-color: #c1c3c5;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.circle_small {
  width: 35px;
  height: 35px;
  border-radius: 50%;
}

.cyan {
  background-color: #91abad;
}

.magenta {
  background-color: #a791ad;
}

.yellow {
  background-color: #adaa91;
}

.key {
  background-color: #2d2d2d;
}

.defend {
  background-image: url("shield.9e60c41a.svg");
  background-repeat: no-repeat;
  background-size: contain;
}

